/**
 * @file
 * A series of standardized elements for possible inclusion in a local theme.
 * Elements presented as mixins so keep the base theme lightweight. This also allows the SASS compiler
 * to use local theme variables.
 */
/**
 * @file
 * Base Theme Animations File
 * Collects useful CSS animations (i.e. @keyframes statements).
 * Most animations should be placed in the _0_local_animations.sass file to avoid loading unused statements.
 */
@keyframes build {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    opacity: 0.5;
    transform: scale(1, 1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1, 1.1);
  }
  100% {
    opacity: 0.5;
    transform: scale(1, 1);
  }
}

@keyframes build-splash {
  from {
    opacity: 0;
    background-color: white;
  }
  to {
    opacity: 0.85;
    background-color: #ffb334;
  }
}

@keyframes masthead-slidedown {
  from {
    height: 0;
  }
  to {
    height: 50;
  }
}

@keyframes discovery-item {
  from {
    opacity: 0;
    background-color: white;
  }
  to {
    opacity: 0.7;
    background-color: #ffb334;
  }
}

@keyframes discovery-item-interior {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* !1. STRUCTURE AND TYPOGRAPHY */
/**
 * @file
 * Base Structure
 * Defines ids and classes to support a basic grid.
 *
 * Version 1.1: Replaces SUSY dependency with Flexbox
 */
/* !VIEWPORT SETUP */
@-webkit-viewport {
  width: device-width;
}

@-moz-viewport {
  width: device-width;
}

@-ms-viewport {
  width: device-width;
}

@-o-viewport {
  width: device-width;
}

@viewport {
  width: device-width;
}

/* SVG Support */
.svg-wrap > img {
  display: none;
}

/* !TEMPLATING RULES */
body.show-grid .region {
  border: 1px solid red;
  box-sizing: border-box;
}

body.show-grid .region {
  min-height: 4.5em;
  box-sizing: border-box;
}

body.show-grid div.region {
  border: 1px dotted green;
}

body.show-grid aside.region {
  border: 1px dotted blue;
}

/* !BASIC STRUCTURE */
html, body {
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
}

section {
  overflow: hidden;
}

div, aside, nav, article, section, header {
  box-sizing: border-box;
}

/* MOBILE */
#mobile-header-nav {
  display: block;
}

@media only screen and (min-width: 56.25em) {
  #mobile-header-nav {
    display: none;
  }
}

#mobile-nav-block {
  display: none;
}

/* ! Sticky Header Class */
.sticky {
  position: fixed;
  top: 0;
}

.sticky-mobile {
  position: fixed;
  top: 0;
}

@media only screen and (min-width: 56.25em) {
  .sticky-mobile {
    position: initial;
    top: initial;
  }
}

/* !BASIC GRID RULES */
/*
  First-level blocks are for meta-structure (overlays, Drupal admin toolbar, etc.).
  The second level is the “page”.
  Third-level are regions, which span the full width.
  Fourth-level blocks are limited to the max-width. */
#content-area, header {
  margin: 0 auto;
}

@media only screen and (min-width: 56.25em) {
  #content-area, header {
    max-width: 1200px;
  }
}

.region {
  margin-top: 0;
  margin-bottom: 0;
}

.region > * {
  padding: 0.75em;
  margin: 0 auto;
}

@media only screen and (min-width: 56.25em) {
  .region > * {
    padding: 1.5em;
    max-width: 1200px;
  }
}

@media only screen and (min-width: 112.5em) {
  .region > * {
    padding: 1.5em 0;
  }
}

.region > * {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
}

@media only screen and (min-width: 56.25em) {
  .region > * {
    flex-direction: row;
  }
}

@media only screen and (min-width: 56.25em) {
  .region > * > aside {
    flex-shrink: 0;
    width: 37%;
  }
}

.region > * > div {
  flex-grow: 1;
}

