/**
 * @file
 * Local Typography
 * Redefinitions of base HTML tags for easier responsive design.
 */

@include font-face('Frutiger LT Std', $asset-base-path + '/fonts/FrutigerLTStd-Light');
@include font-face('Frutiger LT Std Italic', $asset-base-path + '/fonts/FrutigerLTStd-LightItalic','normal','italic');
@include font-face('Frutiger LT Std Roman', $asset-base-path + '/fonts/FrutigerLTStd-Roman');
@include font-face('Frutiger LT Std Roman Italic', $asset-base-path + '/fonts/FrutigerLTStd-Italic','normal','italic');
@include font-face('Frutiger LT Std Bold', $asset-base-path + '/fonts/FrutigerLTStd-Bold','bold');
@include font-face('Frutiger LT Std Bold Italic', $asset-base-path + '/fonts/FrutigerLTStd-BoldItalic','bold','italic');

$basefont: 'Frutiger LT Std', Tahoma, Helvetica, Arial, sans-serif;
$headingfont: 'Frutiger LT Std Roman', Tahoma, Helvetica, Arial, sans-serif;
$boldface: 'Frutiger LT Std Bold', Tahoma, Helvetica, Arial, sans-serif;


html {
  font-family: $basefont;

  @include bp(sml) {
    font-size: 60%; }
  @include bp(med) {
    font-size: 100%; } }

h1, h2, h3, h4, h5, h6 {
  font-family: $headingfont;
  letter-spacing: 0.05em; }
