/**
 * @file _1_local_mixins.sass
 * Theme-specific Mixins
 */

@mixin font-face($family, $path, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$path}.eot');
    src: url('#{$path}.eot?#iefix') format('embedded-opentype'), url('#{$path}.woff') format('woff'), url('#{$path}.ttf') format('truetype');
    font-weight: $weight;
    font-style: $style; } }

@mixin font-size($font-size: 16,$unit: em) {
  font-size: #{$font-size}#{$unit}; }

// Standardized Footer
@mixin standardized-footer() {
  box-sizing: border-box;
  background-color: $black;
  color: $white;
  font-weight: normal;
  width: 100%;
  background-repeat: no-repeat;
  padding: $blh $blh/2;

  @include bp(med) {
    min-height: $blh * 10;
    background-position: $blh $blh;
    padding: $blh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;

    > * {
      width: 100%;

      @include bp(med) {
        width: 33.33%; } }

    @include bp(med) {
      padding-top: 0px; } }

  a {
    color: $white; }

  ul.menu {
    margin: 0;

    li {
      text-align: center;

      @include bp(med) {
        text-align: left; } }

    li:first-child {
      margin-top: 0;
      padding-top: 0; }

    a {
      text-transform: uppercase;
      text-decoration: none; } }

  .region-content {
    padding-top: $blh;

    @include bp(med) {
      padding-top: 1em; } }

  .contact-info {
    padding-top: 1em;
    text-align: center;

    @include bp(med) {
      text-align: left; } } }

// Standardized Footer Region

@mixin standardized-footer-region {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;

  padding: $blh*2;

  @include bp(med) {
    padding-left: 400px; }

  > section {
    padding-top: $blh;
    width: 100%;

    @include bp(med) {
      padding-top: 0;
      width: 66.666%; } }

  > section:last-child {
    width: 100%;

    @include bp(med) {
      width: 66.666%; } } }
