/* Local Discovery UI Changes */

#discovery #discovery-interface #discovery-keyword {
  display: none; }

#review {
  .review-item {
    display: flex;
    flex-direction: column;
    padding: 3em;
    @include bp(med) {
      flex-direction: row;
      padding: 2em 4em; }
    &:nth-child(even) {
      background-color: $neutral; }
    &:last-child {
      margin-bottom: 6em; }
    h6 {
      font-size: 1.5em;
      @include bp(med) {
        font-size: 1.2em; } }
    .criterion {
      width: 100%;
      @include bp(med) {
        width: 25%;
        padding-right: 2em; }
      h6 {
        line-height: 1.5em;
        letter-spacing: 0.05em; } }
    .criterion-detail {
      width: 100%;
      @include bp(med) {
        width: 75%; }
      * {
        line-height: 1.5em;
        letter-spacing: 0.05em; }
      > p.criterion-text {
        margin: 2em 0 0 0;
        width: 100%;
        padding-bottom: 2em; }
      > p.criterion-showanswer {
        margin: 0.5em 0;
        width: 100%;
        font-size: 1em;
        font-family: $headingfont;
        text-transform: uppercase;
        color: $mid_neutral;
        line-height: 0.8em;
        border-top: solid 1px $mid_neutral;
        padding-top: 0.5em;
        .resourceicon {
          font-size: 1.3em;
          background-color: $mid_neutral;
          height: 0.8em;
          width: 0.8em;
          padding: 0.1em;
          margin-right: 0.2em;
          img {
            width: 0.8em;
            height: 0.8em;
            &.down-arrow {
              transform: rotate(90deg); }
            &.up-arrow {
              transform: rotate(270deg); } } }
        .reviewansweraction {
          cursor: pointer; } } } } }

#review_header {
  font-size: 2em;
  @include bp(med) {
    font-size: 1em; }
  h6 {
    text-transform: uppercase; }
  #review_author {
    margin: 0.5em auto; }
  #review_date {
    font-size: 1.3em; }
  #review_copyright {
    font-size: 1.1em;
    color: $mid_neutral;
    text-transform: uppercase;
    margin-top: 2em;
    font-family: $headingfont;
    a {
      color: $mid_neutral;
      &:hover {
        color: $darker_neutral;
        text-decoration: underline; } }
    img {
      height: 1em;
      width: 1em;
      vertical-align: middle;
      filter: invert(.5);
      margin-right: 1em; } } }

.ratings {
  unicode-bidi: bidi-override;
  color: $white;
  font-size: 9em;
  width: 3em;
  margin: 0 auto;
  position: relative;
  padding: 0;
  text-shadow: -1px 0 $clr4, 0 1px $clr4, 1px 0 $clr4, 0 -1px $clr4;
  @include bp(med) {
    font-size: 5em;
    text-shadow: -2px 0 $clr4, 0 2px $clr4, 2px 0 $clr4, 0 -2px $clr4; }
  .ratings-top {
    color: $clr4;
    padding: 0;
    position: absolute;
    z-index: 1;
    display: block;
    top: 0;
    left: 0;
    overflow: hidden; }
  .ratings-bottom {
    padding: 0;
    display: block;
    z-index: 0; } }

#overall_rating {
  border-bottom: solid 1px $black;
  margin-bottom: 1em;
  padding-bottom: 4em;
  .ratings {
    * {
      line-height: 1em; } }
