    font-size: 2em;
    font-family: $headingfont;
    @include bp(med) {
      font-size: 1em; } } }

#reviews_preview {
  .ratings {
    font-size: 3em;
    width: 3.1em; } }

.criterion {
  .criterion-rating {
    font-size: 1em;
    height: 4em;
    @include bp(med) {
      font-size: 0.7em; }
    * {
      line-height: 0.6em; }
    .ratings {
      margin: 0;
      font-size: 4em;
      width: 3.1em;
      text-shadow: -1px 0 $clr4, 0 1px $clr4, 1px 0 $clr4, 0 -1px $clr4; } } }
